<template>
  <div id="app" :class="{ 'sidebar-opened': showSidebar }">

    <header-component 
      v-on:search-tracks="searchTracks"
      v-on:toggle-sidebar="toggleSidebar">
    </header-component>

    <sidebar-component :show="showSidebar"></sidebar-component>

    <div class="content">

      <banner-component></banner-component>

      <div class="track-list grid grid-cols-2 md:grid-cols-5 gap-4">
          <div v-for="(track, index) in tracks" :key="index" class=" mb-10">
              <div class="track-list__item" @click.prevent="playSong(index)">
                  <div class="track-img">
                      <img class="w-full" :src="track.album.cover_big" alt="album-img">
                  </div>
                  <p class="track-title">
                      {{ track.title }}
                  </p>
                  <p class="track-author">{{ track.artist.name  }}</p>
              </div>
          </div>
      </div>

    </div>

    <controls-component v-on:change-song="playSong" v-on:change-volumen="changeVolumen"  v-on:toggle-play="togglePlay" :track_status="trackPlayStatus" :track="currentSong"></controls-component>

  </div>
</template>

<script>

import {Api}  from '@/util/api'
import axios from 'axios'


import BannerComponent from './components/BannerComponent.vue'
import ControlsComponent from './components/ControlsComponent.vue'
import HeaderComponent from "./components/HeaderComponent.vue"
import SidebarComponent from './components/SidebarComponent.vue'


export default {
  name: 'App',
  components: {
    HeaderComponent,
    SidebarComponent,
    BannerComponent,
    ControlsComponent,
  },
  data:() => {
    return {
      showSidebar: false,
      trackPlayStatus: false,
      currentSong: {
        title: '',
        album: {
          title: '',
        },
        artist: {
          name: ''
        }
      },
      currentSongPlaying: null,
      tracks: []
    }
  },
  mounted(){
    this.getTracks()
  },
  methods:{
    toggleSidebar(){
      this.showSidebar = !this.showSidebar
    },
    togglePlay(){
      if(this.trackPlayStatus){
        this.currentSongPlaying.pause() 
      }else{
        this.currentSongPlaying.play()
      }
      this.trackPlayStatus = !this.trackPlayStatus
    },
    playSong(track_index){
      if(this.currentSongPlaying){
        this.currentSongPlaying.pause()
        this.currentSongPlaying.currentTime = 0;  
      }
      this.currentSongPlaying = new Audio(this.tracks[track_index].preview)
      this.currentSongPlaying.play()

      this.currentSong = this.tracks[track_index]
      this.currentSong.prev = track_index - 1
      this.currentSong.next = track_index + 1
      
      this.trackPlayStatus = !this.trackPlayStatus
      
    },
    async getTracks(){
      let api = new Api('playlist/908622995')
      let res = await axios.get(api.getUrl())
      try {
          this.tracks = res.data.tracks.data.filter( (track) => track.preview != '' )
      } catch (error) {
          console.log(error)
      }
    },
    async searchTracks(search){
      try {
        let api = new Api(`search?q=${search}`)
        let res = await axios.get(api.getUrl())
        this.tracks = res.data.data
      } catch (error) {
          console.log(error)
      }
    },
    changeVolumen(value){
      if(this.currentSongPlaying){
        this.currentSongPlaying.volume = value
      }
      console.log(value)
    }
  }
}
</script>

