<template>
    <div class="controls grid grid-cols-12">
        <div class="controls__col col-span-3 current-song">
            <div class="md:grid grid-cols-5">
                <div class="current-song__img">
                    <img src="https://blogmistermusic.files.wordpress.com/2016/03/bad-michael-jackson.jpg" alt="">
                </div>
                <div class="current-song__info col-span-4">
                    <div>
                        <p class="track-title">{{ track.title }}</p>
                        <p class="track-info">{{ track.artist.name }} - {{ track.album.name }}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="controls__col col-span-6  main-controls">
            <div class="prev">
                <i class='icon bx bx-skip-previous' @click.prevent="changeSong(track.prev)"></i>
            </div>
            <div class="play" @click.prevent="togglePlay">
                <i class='icon bx' :class="[track_status ? 'bx-play' : 'bx-pause']"></i>
            </div>
            <div class="next">
                <i class='icon bx bx-skip-next' @click.prevent="changeSong(track.next)"></i>
            </div>
        </div>
        <div class="controls__col col-span-3  volumen">
            <input class="volumen__input" type="range" min="0" max="1" step="0.1" @change="changeVolumen">
        </div>
    </div>
</template>

<script>
export default {
    props: ['track', 'track_status'],
    methods: {
        togglePlay(){
           this.$emit('toggle-play') 
        },
        changeSong(track_index){
            this.$emit('change-song', track_index)
        },
        changeVolumen({target}){
            this.$emit('change-volumen', target.value)
        }
    }
}
</script>

<style lang="scss" scoped>
    .controls{
        min-height: 10vh; 
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        background-color: #EB5757;
        /* display: flex; */ 
        /* justify-content: space-around; */
        align-items: center;
        padding: 0 4%;

        &__col {
            display: flex;
            align-items: center;
        }

        .current-song{

            &__info{
                display: flex;
                align-items: center;

                .track-title{
                    color: #fff
                }
                .track-info{
                    color: #fff;
                    font-size: 12px;
                }
            }
            
            &__img{
                display: none;
            }
            &__img img{
                width: 100%;
            }
        }

        .main-controls{
            color: #fff;
            justify-content: center;

            .icon {
                font-size: 2rem;
                cursor: pointer;
            }

            .play {
                background-color: #FF7676;
                border-radius: 50%;
                height: 40px;
                width: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 10px;

                .icon{
                    font-size: 1.5rem;
                }
            }
        }

        .volumen{
            justify-content: flex-end;

            &__input {
                -webkit-appearance: none;
                -moz-appearance: none;
                width: 50%;
                height: 5px;
                padding: 0;
                border-radius: 2px;
                outline: none;
                cursor: pointer;
            }

            &__input::-webkit-slider-thumb {
                -webkit-appearance: none;
                -moz-appearance: none;
                -webkit-border-radius: 5px;
                /*16x16px adjusted to be same as 14x14px on moz*/
                height: 16px;
                width: 16px;
                border-radius: 50%;
                background: #e7e7e7;
                border: 1px solid #c5c5c5;
            }
        }

        
    }
    @media (min-width: 768px) { 
        .controls{
            padding-left: 0;
            padding-right: 0;
        }

        .current-song__img{
            display: block;
        }
        .current-song__info{
            padding-left: 4%;
        }
        .volumen{
            padding: 0 3%;
        }
        .volumen__input{
            width: 30%;
        }
    }

    

</style>