                    <input type="text" class="form-search__input" placeholder="Buscar" @change="searchTracks" >
                    <i class='form-search__icon bx bx-search-alt-2'></i>
                </div>
            </div>
            <!-- -->
            <div class="header__user d-none md:d-block col-span-6">
                <p class="user">
                    <i class='icon bx bxs-user'></i>
                    Andres G.
                </p>
            </div>
        </div>

