</template>

<script>
export default {
    methods: {
        toggleSidebar(){
            this.$emit('toggle-sidebar')
        },
        searchTracks({ target }){
            this.$emit('search-tracks', target.value)
        }
    }
}
</script>

<style lang="scss" scoped>
    .header{
        width: 100%;
        /* position: fixed; */
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1rem 3%;
        /***/
        &__toggle{
            cursor: pointer;
            font-size: 1.5rem;
        }
        &__controls{
            width: 100%;
        }
        &__user{
            display: none;
            align-items: center;
            font-size: 16px;
            justify-content: flex-end;
            .icon{
                color: #E86060;
            }
            .user{
                color: #4F4F4F;
                font-size: 16px;
            }
        }
        @media (min-width: 768px) { 

            .header__toggle{
                display: none;
            }
